<template>
  <div class="loader-wrapper" v-if="isLoading">
    <BaseLoader />
  </div>
  <div v-else-if="isLoadingFailed">
    <h1>    .   !</h1>
  </div>
  <div class="content__top" v-else>
    <ul class="breadcrumbs">
      <li class="breadcrumbs__item">
        <router-link class="breadcrumbs__link" :to="{ name: 'home' }">
          
        </router-link>
      </li>
      <li class="breadcrumbs__item">
        <a class="breadcrumbs__link" :to="{ name: 'cart' }">
          
        </a>
      </li>
      <li class="breadcrumbs__item">
        <a class="breadcrumbs__link">
           
        </a>
      </li>
    </ul>

    <h1 class="content__title">
        <span> {{ orderInfo.id }}</span>
    </h1>
  </div>

  <section class="cart">
    <form class="cart__form form" action="#" method="POST">
      <div class="cart__field">
        <p class="cart__message">
           &nbsp;  . &nbsp;    &nbsp; .
             &nbsp; &nbsp;     .
        </p>

        <ul class="dictionary">
          <li class="dictionary__item">
            <span class="dictionary__key">
              
            </span>
            <span class="dictionary__value">
              {{ orderInfo.name }}
            </span>
          </li>
          <li class="dictionary__item">
            <span class="dictionary__key">
               
            </span>
            <span class="dictionary__value">
              {{ orderInfo.address }}
            </span>
          </li>
          <li class="dictionary__item">
            <span class="dictionary__key">
              
            </span>
            <span class="dictionary__value">
              {{ orderInfo.phone }}
            </span>
          </li>
          <li class="dictionary__item">
            <span class="dictionary__key">
              Email
            </span>
            <span class="dictionary__value">
              {{ orderInfo.email }}
            </span>
          </li>
          <li class="dictionary__item">
            <span class="dictionary__key">
               
            </span>
            <span class="dictionary__value">
              {{ orderInfo.paymentType }}
            </span>
          </li>
        </ul>
      </div>

      <div class="cart__block">
        <ul class="cart__orders">
          <li class="cart__order" v-for="item in orderInfo.basket.items" :key="item.id">
            <h3>{{ item.product.title }}</h3>
            <b>{{ item.product.price }} </b>
            <span>: {{ item.product.id }}</span>
          </li>
        </ul>

        <div class="cart__total">
          <p>: <b>{{ +orderInfo.deliveryType.price ? `${orderInfo.deliveryType.price} ` : '' }}</b>
          </p>
          <p>: <b>{{ orderInfo.basket.items.length }}</b>    <b>{{ orderInfo.totalPrice }} </b></p>
        </div>
      </div>
    </form>
  </section>
</template>

<script>
import BaseLoader from '@/components/BaseLoader.vue';

export default {
  components: { BaseLoader },
  computed: {
    orderInfo() {
      return this.$store.state.orderInfo;
    },
    isLoading() {
      return this.$store.state.isLoading;
    },
    isLoadingFailed() {
      return this.$store.state.isLoadingFailed;
    },
  },
  created() {
    this.$store.dispatch("loadOrderInfo", this.$route.params.id);
  }
}
</script>
